import React, { Component } from 'react';
import {NavLink,Link,Route,Switch} from 'react-router-dom';
import Main from './Components/HomePage/Main';
import ProjectMainPage from './Components/ProjectPage/ProjectMainPage';

const Nomatch = () => (
    <div>
        <h1 className="Nomatch">Error Code: 404<br />
            <small>Page Not Found</small>
        </h1>
    </div>
);

export default class AdvancedHome extends Component {
  render() {
    return (
      <div>
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
            <NavLink to="/Home" className="nav-item links" 
            activeClassName="active">Home</NavLink>
            <NavLink to="/ProjectMainPage" className="nav-item links">Projects</NavLink>
        </nav>
        <Switch>
            <Route path="/Home" component={Main} />
            <Route path="/ProjectMainPage" component={ProjectMainPage} />
            <Route component={Nomatch} />
        </Switch>
      </div>
    )
  }
}
